<template>
    <div id="container">
    </div>
</template>
  
<script>
import AMapLoader from '@amap/amap-jsapi-loader';

export default {

    props: {
        position: {
            type: Array,
            default: [0, 0]
        }
    },
    name: 'Map',

    data() {
        return {
            infoWindow: null,
            map: null,// 

        }
    },

    mounted() {
        this.initMap();
    },

    methods: {
        initMap() {
            AMapLoader.load({
                key: "0a72a0fa26c13e8fa8581cbe701c0620",             // WebKey load 
                version: "2.0",      //  JSAPI  1.4.15
                plugins: ['AMap.Scale'],       // 'AMap.Scale'
            }).then((AMap) => {
                this.map = new AMap.Map("container", {  //id
                    viewMode: "3D",    //3D
                    zoom: 10,           //
                    center: this.position, //
                });

                this.infoWindow = new AMap.InfoWindow({ offset: new AMap.Pixel(0, -30) });

                var marker = new AMap.Marker({
                    position: this.position,
                    map: this.map
                })

            }).catch(e => {
                // console.log(e);
            });
        },

        markerClick(e) {
            this.infoWindow.setContent(e.target.content);
            this.infoWindow.open(this.map, e.target.getPosition());
        }
    },
};
</script>

<style lang="less" scoped>
#container {
    width: 100%;
    height: 200px;
}

.t_card_main {
    width: 100%;
    height: 100%;
}
</style>

