<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span style="font-weight:bold;font-size:25px">{{ title }}</span>
        </div>

        <el-row class="table_header">
            <!--  -->
            <div v-if="search_box_visibale && user_local !== 'list' && user_local !== 'administrator'" style="width:100vw">
                <el-row class="header_row">
                    <el-col :span="3" style="line-height:40px;">
                        {{ user_local === 'car' ? '' : title.substring(0, title.length - 2) + '' }}
                    </el-col>
                    <el-col :span="5">
                        <el-input v-model="search_input" size="medium"></el-input>
                    </el-col>
                    <el-col :span="11" style="margin-left:5px">
                        <el-button type="primary" @click="search" icon="el-icon-search" size="medium"
                            :disabled="search_input == ''"></el-button>
                    </el-col>
                    <el-col :span="5">
                    </el-col>
                </el-row>
            </div>

            <!--  -->
            <div v-else-if="user_local !== 'list' && user_local !== 'administrator'" style="width:100vw">
                <el-row class="header_row">
                    <el-col :span="2" style="line-height:40px;font-weight:bold">
                        
                    </el-col>
                    <el-col :span="22">
                        <el-radio-group v-if="user_local === 'warning_msg'" v-model="filter_res" size="medium"
                            @input="() => $emit('filter_first', filter_res)">
                            <el-radio-button v-for="item in warning_checkbox_item" :label="item" :key="item">{{ item
                            }}</el-radio-button>
                        </el-radio-group>
                        <el-radio-group v-else-if="user_local === 'complaints'" v-model="filter_res" size="medium"
                            @input="() => $emit('filter_three', filter_res)">
                            <el-radio-button v-for="item in complaints_check_item" :label="item" :key="item">{{ item
                            }}</el-radio-button>
                        </el-radio-group>
                        <el-radio-group v-else v-model="filter_res" size="medium"
                            @input="() => $emit('filter_second', filter_res)">
                            <el-radio-button v-for="item in checkbox_item" :label="item" :key="item">{{ item
                            }}</el-radio-button>
                        </el-radio-group>
                    </el-col>
                </el-row>
            </div>

            <!--  -->

            <div v-else-if="user_local === 'administrator'" style="width:100vw">
                <el-row class="header_row">
                    <el-col :span="2" style="line-height:40px;">
                        :
                    </el-col>
                    <el-col :span="6">
                        <el-select v-model="enterprise_id" placeholder="" size="medium">
                            <el-option v-for="item in enterprise_options" :key="item.id" :label="item.enterprise_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="11" style="margin-left:5px">
                        <el-button type="primary" @click="search" icon="el-icon-search" size="medium"></el-button>
                    </el-col>
                    <el-col :span="5">
                    </el-col>
                </el-row>
            </div>

            <!--  -->
            <el-button type="success" class="add_btn" @click="() => $emit('is_add_dialog_visible', true)"
                icon="el-icon-circle-plus-outline" v-if="add_btn_visible && user_local !== 'list'"
                size="medium"></el-button>
        </el-row>

        <!--  -->
        <el-table :data="data.length == 0 ? table_data : data" border stripe empty-text="" fit
            :default-sort="{ prop: 'send_time', order: 'descending' }">
            <el-table-column label="" width="50" align="center">
                <template slot-scope="scope">
                    {{ scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column v-for="item in table_item" :key="item.id" :prop="item.key" :label="item.value" resizable
                show-overflow-tooltip :width="item.width" align="center"
                :sortable="item.key === 'send_time' || item.key === 'star' || (item.key === 'name' && user_local === 'tour_guide_application') ? true : false">
            </el-table-column>
            <el-table-column v-if="user_local === 'tour_guide_application'" label="" align="center">
                <template slot-scope="scope">
                    {{ table_data[scope.$index].if_succeed === '0' ? '' : (table_data[scope.$index].if_succeed === '1' ?
                        '' : '')
                    }}
                </template>
            </el-table-column>
            <el-table-column v-if="user_local === 'complaints'" label="" align="center">
                <template slot-scope="scope">
                    {{ table_data[scope.$index].if_succeed === '1' ? '' : '' }}
                </template>
            </el-table-column>

            <el-table-column v-if="user_local === 'warning_msg'" label="" align="center">
                <template slot-scope="scope">
                    {{ table_data[scope.$index].need_cope === '0' ? '' : (table_data[scope.$index].need_cope === '1' ?
                        '' : (table_data[scope.$index].need_cope === '2' ? '' : ''))
                    }}
                </template>
            </el-table-column>
            <el-table-column fixed="right" label="" width="150" align="center" v-if="oper_visible">
                <!--  -->
                <template slot-scope="scope">
                    <!--  -->
                    <el-button v-if="user_local === 'travel_agency' && user_local !== 'list'" @click="get_guide(scope.row)"
                        type="success" size="mini" style="padding:5px"></el-button>
                    <!--  \\  -->
                    <el-button
                        v-if="user_local !== 'warning_msg' && user_local !== 'tour_guide_application' && user_local !== 'complaints' && user_local !== 'list'"
                        @click="add_dialog_visible(scope.row)" type="primary" size="mini" style="padding:5px"></el-button>
                    <!--  \  -->
                    <el-button v-else-if="user_local !== 'list'" @click="show_deal_with(scope.row)" type="primary"
                        size="mini" style="padding:5px"></el-button>

                    <el-button v-if="user_local !== 'list'" @click="show_delete(scope.row)" type="danger" size="mini"
                        style="padding:5px"></el-button>

                    <el-button v-if="user_local == 'list'"
                        @click="() => $router.push({ path: '/user/itinerary_detail', query: { itinerary_id: scope.row.id, title: '' } })"
                        type="primary" size="mini" style="padding:5px"></el-button>
                    <el-button v-if="user_local == 'list'" @click="() => $emit('row_id', scope.row.id)" type="success"
                        size="mini" style="padding:5px"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination v-if="user_local !== 'list'" align='right' @size-change="handleSizeChange"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[1, 5, 10, 20]"
            :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>

        <!--  -->
        <el-dialog title="" :visible.sync="delete_box_visible" width="20%" center top="30vh">
            <p></p>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delete_box_visible = false"> </el-button>
                <el-button type="danger" @click="delete_row"> </el-button>
            </span>
        </el-dialog>

        <!-- , -->
        <el-dialog title=""
            v-if="(user_local === 'complaints' || user_local === 'warning_msg' || user_local === 'tour_guide_application') && deal_with_dialog_visible"
            :visible.sync="deal_with_dialog_visible" :width="width" center>
            <el-card v-if="user_local === 'complaints' || user_local === 'warning_msg'">
                <div slot="header">
                    <h3>{{ deal_with_item.title }}</h3>
                    <span style="font-size:5px; color:rgb(122, 122, 122)">{{ deal_with_item.send_time
                    }} | {{ deal_with_item.sender }}</span>
                    <el-tag v-if="user_local === 'complaints'" style="float: right;" type="success">{{
                        deal_with_item.enterprise }}</el-tag>
                </div>
                <p>{{ deal_with_item.content }}</p>
            </el-card>

            <el-card v-if="user_local === 'tour_guide_application' && deal_with_dialog_visible">
                <Description :deal_with_item="deal_with_item" />
            </el-card>

            <span slot="footer" class="dialog-footer">
                <el-button @click="deal_with_dialog_visible = false" size="medium"> </el-button>
                <el-button type="warning" @click="deal_with_row('0')" size="medium"></el-button>
                <el-button type="success" @click="deal_with_row('1')" size="medium"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" v-if="user_local === 'travel_agency' && on_duty_guide_dialog_visible"
            :visible.sync="on_duty_guide_dialog_visible" width="50%" center>
            <el-table :data="on_duty_guide_data" border stripe empty-text="" fit height="500px">
                <el-table-column label="" width="50" align="center">
                    <template slot-scope="scope">
                        {{ (scope.$index + 1) }}
                        <!-- {{ (scope.$index + 1) + (currentPage - 1) * pageSize }} -->
                    </template>
                </el-table-column>
                <el-table-column v-for="item in on_duty_guide_table_item" :key="item.id" :prop="item.key"
                    :label="item.value" resizable show-overflow-tooltip :width="item.width" align="center">
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="on_duty_guide_dialog_visible = false" size="medium"> </el-button>
            </span>
        </el-dialog>

    </el-card>
</template>


<script lang="js">
import Description from '@/components/reusable_components/description.vue'
export default {
    name: 'TableCard',

    components: {
        Description
    },
    props: {
        title: {
            type: String,
            default: '',
        },

        total: {
            type: Number,
            default: 0
        },

        user: {
            type: String,
            default: ''
        },

        table_item: {
            type: Array,
            default: null
        },

        table_data: {
            type: Array,
            default: null,
        },

        search_box_visibale: {
            type: Boolean,
            default: true
        },

        add_box_visible: {
            type: Boolean,
            default: false
        },

        add_btn_visible: {
            type: Boolean,
            default: true
        },

        oper_visible: {
            type: Boolean,
            default: true
        }
    },


    data() {
        return {
            currentPage: 1, // 
            pageSize: 10, // 
            data: [],
            search_input: '',
            filter_res: '',

            checkbox_item: ['', '', '', ''],
            complaints_check_item: ['', '', ''],
            warning_checkbox_item: ['', '', '', '', ''],
            is_add_dialog_visible: false,
            user_local: '',// 
            delete_box_visible: false, // 
            delete_target: null, // 
            deal_with_dialog_visible: false, // 
            deal_with_target: null, // 
            deal_with_item: null, // 
            on_duty_guide_dialog_visible: false, // 
            on_duty_guide_data: [],// 
            on_duty_guide_table_item: [
                { key: 'name', value: '', },
                { key: 'gender', width: '50px', value: '', },
                { key: 'information', value: '', },
                { key: 'phone', value: '', },
            ],

            enterprise_options: [
                { id: 0, enterprise_name: '' },
                { id: 1, enterprise_name: '' },
            ],
            enterprise_id: null,
        }
    },

    computed: {
        width() {
            return this.user_local === 'tour_guide_application' ? '50%' : '30%'
        }
    },
    mounted() {

    },
    created() {
        this.data = this.table_data;
        this.currentPage = 1;
        this.pageSize = 10;
        this.user_local = this.user
    },

    methods: {
        // 
        handleSizeChange(val) {
            this.currentPage = 1;
            this.pageSize = val;
            this.$emit("currentPage", val)
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.$emit("currentPage", val)
        },

        async get_all_enterprise() {
            try {
                const res = await this.$http.post("/findAdministratorByName", {// user
                    // 
                    name: this.search_input
                });
                this.data = []
                this.data.push(res.data.data)
            } catch (error) {
                // console.log(error);
            }
        },


        // 
        async search() {
            let url = ''
            switch (this.user_local) {
                case 'scenic_spot':
                case 'hotel':
                case 'travel_agency':
                    url = '/findTourismEnterpriseByName'
                    break;
                case 'administrator':
                    url = '/findAdministratorByName'
                    break;
                case 'car':
                    url = '/findRescueVehicleByLicenseNumber'
                    break
                default:
                    break;
            }
            try {
                const res = await this.$http.post(url, {// user
                    // 
                    name: this.search_input
                });
                console.log(res)
                this.data = []
                this.data.push(res.data.data)
            } catch (error) {
                // console.log(error);
            }
        },
        // 
        add_dialog_visible(row) {
            this.$emit("is_add_dialog_visible", true)
            this.$emit("row_id", row.id)
        },

        // 
        show_delete(row) {
            this.delete_box_visible = true
            this.delete_target = row
        },

        // 
        show_deal_with(row) {
            this.get_deal_with_item(row)
            this.deal_with_target = row
            this.deal_with_dialog_visible = true
        },

        // 
        get_deal_with_item(row) {
            this.deal_with_item = row
        },

        async delete_row() {
            let url = ''
            switch (this.user_local) {
                case 'scenic_spot':
                case 'hotel':
                case 'travel_agency':
                    url = '/deleteTourismEnterprise'
                    break;
                case 'tour_guide_application':
                    url = '/deleteApplicationById'
                    break;
                case 'on_duty_tour_guide':
                    url = '/deleteGuideById'
                    break

                default:
                    break;
            }
            try {
                const res = await this.$http.delete(url, { // user
                    // 
                    data: {
                        id: this.delete_target.id
                    }
                });
                if (res.data.code === 2000) {
                    this.$message.success('')
                } else {
                    this.$message.error('')
                }
                console.log(res)
                this.delete_box_visible = false
            } catch (error) {
                // console.log(error);
            }
        },

        // 
        async deal_with_row(res_) {
            let url = ''
            switch (this.user_local) {
                case 'tour_guide_application':
                    url = '/reviewApplication'
                    break;

                case 'complaints':
                    url = '' // 
                    break
                // case 'tour_guide_application':
                //     url = '/reviewApplication'
                //     break;
                default:
                    break;
            }
            console.log(url)
            try {
                const res = await this.$http.post(url, { // user
                    // 
                    id: this.deal_with_item.id + '',
                    if_succeed: res_
                });

                if (res.data.code === 2000) {
                    this.$message.success('')
                }
                // 
                this.deal_with_dialog_visible = false
            } catch (error) {
                // console.log(error);
            }
        },

        // 
        async get_guide(row) {
            this.on_duty_guide_dialog_visible = true
            this.on_duty_guide_data = []
            console.log(row.id)
            try {
                const res = await this.$http.post("/findGuideByAgency_id", {
                    // 
                    agency_id: row.id
                });
                console.log(res)
                this.on_duty_guide_data = res.data.data
            } catch (error) {
                // console.log(error);
            }
        },
    },
    watch: {
        user_local(news, olds) {
            // console.log(news)
        }
    }
}
</script>

<style lang="less" scoped>
.table_header {
    display: flex;
    justify-content: space-between;

    .add_btn {
        margin-bottom: 5px;
        height: 40px
    }

    .header_row {
        margin-bottom: 5px;
        display: flex;
        justify-content: space-between;
    }
}

.el-table .warning-row {
    background: oldlace;
}

.el-table .success-row {
    background: #f0f9eb;
}
</style>
